<template>
  <v-app light>
    <v-navigation-drawer v-model="drawer" app absolute temporary>
      <v-list>
        <v-list-tile v-for="item in items" :key="item.title" router-link :to="item.to" exact>
          <v-list-tile-action>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title class="title">
              {{ item.title }}
            </v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
    <v-toolbar fixed app color="primary">
      <v-toolbar-side-icon class="hidden-sm-and-up" @click="drawer = !drawer" />
      <v-toolbar-title>
        <router-link to="/" tag="span" style="cursor: pointer">
          {{ title }}
        </router-link>
      </v-toolbar-title>
      <v-spacer />
      <v-toolbar-items class="hidden-xs-only">
        <v-btn flat router to="/">
          <v-icon left>
            fas fa-home
          </v-icon>Home
        </v-btn>
        <v-btn flat router to="/about">
          <v-icon left>
            fas fa-clipboard-list
          </v-icon>About
        </v-btn>
        <v-btn flat router to="/projects">
          <v-icon left>
            fas fa-code
          </v-icon>Projects
        </v-btn>
      </v-toolbar-items>
    </v-toolbar>
    <v-content>
      <v-container>
        <router-view/>
      </v-container>
      <v-fab-transition>
        <v-btn
          v-show="fab"
          v-scroll="onScroll"
          color="secondary"
          fab
          dark
          small
          fixed
          bottom
          right
          @click="toTop"
        >
          <v-icon>
            fas fa-angle-up
          </v-icon>
        </v-btn>
      </v-fab-transition>
    </v-content>
    <v-footer
      app
      height="auto"
      
    >
      <v-layout
        justify-center
        row
        wrap
      >
        <v-flex
          accent
          py-3
          text-xs-center
          dark--text
          xs12
        >
          &copy;2019 — <strong>Tomcatbuzz</strong>
        </v-flex>
      </v-layout>
    </v-footer>
  </v-app>
</template>

<script>
export default {
  data() {
    return {
      fab: false,
      drawer: null,
      items: [
        { icon: 'fas fa-home', title: 'Home', to: '/' },
        { icon: 'fas fa-clipboard-list', title: 'About', to: '/about' },
        { icon: 'fas fa-code', title: 'Projects', to: '/projects' }
      ],
      title: 'Tomcatbuzz'
    }
  },
  methods: {
    onScroll() {
      if (typeof window === 'undefined') return
      const top = window.pageYOffset || document.documentElement.offsetTop || 0
      this.fab = top > 20
    },
    toTop() {
      this.$vuetify.goTo(0)
    }
  }
}
</script>
